$bgColor: #001a2b
$bgElColor: #193343
$textPinkColor: #cf52a1
$textCyanColor: #52aecf
$modalBgColor: #6e8391
$modalTextColor: #001a2b

@font-face 
  font-family: 'Proxima Nova Lt'
  src: url('./fonts/ProximaNova-Light.eot')
  src: local('Proxima Nova Light')
  src: local('ProximaNova-Light')
  src: url('./fonts/ProximaNova-Light.eot?#iefix') format('embedded-opentype')
  src: url('./fonts/ProximaNova-Light.woff') format('woff')
  src: url('./fonts/ProximaNova-Light.ttf') format('truetype')
  font-weight: 300
  font-style: normal

@font-face 
  font-family: 'Proxima Nova Rg'
  src: url('./fonts/ProximaNova-Regular.eot')
  src: local('Proxima Nova Regular')
  src: local('ProximaNova-Regular')
  src: url('./fonts/ProximaNova-Regular.eot?#iefix') format('embedded-opentype')
  src: url('./fonts/ProximaNova-Regular.woff') format('woff')
  src: url('./fonts/ProximaNova-Regular.ttf') format('truetype')
  font-weight: normal
  font-style: normal

@font-face 
  font-family: 'Proxima Nova Rg'
  src: url('./fonts/ProximaNova-RegularIt.eot')
  src: local('Proxima Nova Regular Italic')
  src: local('ProximaNova-RegularIt')
  src: url('./fonts/ProximaNova-RegularIt.eot?#iefix') format('embedded-opentype')
  src: url('./fonts/ProximaNova-RegularIt.woff') format('woff')
  src: url('./fonts/ProximaNova-RegularIt.ttf') format('truetype')
  font-weight: normal
  font-style: italic


html, body, h1, h2
  margin: 0
  padding: 0

html
  font-size: 16px
  @media (max-width: 600px)
    font-size: 8px
html *::selection 
  color: #193343
  background-color: $textCyanColor

body
  background-color: $bgColor
  font-family: 'Proxima Nova Rg', Arial
  color: $textCyanColor

ul, li
  margin: 0
  padding: 0
  list-style: none


.home
  width: 100vw
  height: 100vh
  position: relative
  background-color: rgba(0,0,0,0.4)
  background-image: url('./img/bg.png')
  background-repeat: no-repeat
  background-position: center bottom
  background-size: contain
  &__bg
    position: absolute
    z-index: -1
    margin: 0
    padding: 0
    width: 100%
    height: 100%
  &__title
    position: relative
    left: 50%
    top: 50%
    transform: translate(-50%, -50%)
    width: fit-content
    text-align: center
  &__logo
    max-width: 80%
  &__heading
    font-size: 6rem
    color: $textPinkColor
    font-weight: 300
    @media (max-width: 600px)
      font-size: 6rem
  &__subheading
    font-size: 4rem
    font-family: 'Proxima Nova Lt'
  &__name
    color: $textCyanColor
  .notification
    position: fixed
    display: inline
    cursor: pointer
    user-select: none
    z-index: 1
    width: 100%
    top: 0
    font-size: 3rem
    line-height: 4rem
    text-align: center
    background-color: #6e839180
    transition: 1s
    font-family: 'Proxima Nova Rg'
    font-style: italic
    @media (max-width: 600px)
      font-size: 3.5rem
    &.hide
      top: -4rem
    &.no-js
      top: unset

.modal
  transform: scale(0)
  position: absolute
  display: block
  height: 100%
  width: 100%
  top: 0
  left: 0
  z-index: 1
  &__bg
    z-index: 1
    background-color: rgba(0,0,0,0.6)
    width: 100vw
    height: 100vh
  &__box
    position: absolute
    left: 50%
    top: 50%
    transform: translate(-50%, -50%)
    background-color: $modalBgColor
    padding: 40px 60px
    width: 80%
    height: 100%
    max-width: 800px
    max-height: 80%
    box-sizing: border-box
    @media (max-width: 600px)
      padding: 10px 40px
  &__svg
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: -1
    width: 100%
    height: 100%
    rect
      stroke: $textPinkColor
      stroke-width: 8px
      stroke-dasharray: 300
      stroke-dashoffset: 300
  &__content
    text-align: center
    height: 100%
    max-height: 100%
    overflow: hidden
    color: $modalTextColor
    margin-right: -8px
    h1
      font-size: 2.5rem
      margin-bottom: 2rem
      @media (max-width: 600px)
        font-size: 4rem
        margin-bottom: 5rem
  &__close
    position: absolute
    cursor: pointer
    right: 10px
    top: 10px
    width: 40px
    height: 40px
    background: none
    border: none
    margin: 0
    padding: 0
    &:before, &:after
      display: block
      position: absolute
      content: ''
      width: 100%
      height: 2px
      background-color: $modalTextColor
    &:before
      transform: rotate(45deg)
    &:after
      transform: rotate(-45deg)

.modal-active
  .modal
    transform: scale(1)
    &__bg
      background: rgba(0, 0, 0, 0)
      animation: fadeIn 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) forwards
    &__box
      background-color: transparent
      animation: modalFadeIn 0.5s 0.8s cubic-bezier(0.165, 0.84, 0.44, 1) forwards
    &__content
      opacity: 0
      position: relative
      animation: modalContentFadeIn 0.5s 1s cubic-bezier(0.165, 0.84, 0.44, 1) forwards
    &__close
      opacity: 0
      animation: modalCloseBtnFadeIn 0.5s 1s cubic-bezier(0.165, 0.84, 0.44, 1) forwards
    &__svg > rect
      opacity: 1
      animation: sketchIn 2s cubic-bezier(0.165, 0.84, 0.44, 1) forwards

.modal-active.out
  .modal
    visibility: hidden
    z-index: -1
    transition: z-index 2s, visibility 0.2s
    &__bg
      animation: fadeOut .5s cubic-bezier(0.165, 0.840, 0.440, 1.000) forwards
    &__box
      animation: modalFadeOut .5s cubic-bezier(0.165, 0.840, 0.440, 1.000) forwards
    &__content, &__close
      animation: modalContentFadeOut .5s cubic-bezier(0.165, 0.840, 0.440, 1.000) forwards
    &__svg > rect
      display: none

.project
  &__list
    display: flex
    flex-wrap: wrap
    justify-content: space-between
    margin-left: -8px
    max-height: 78%
    overflow-y: scroll
    &::-webkit-scrollbar
      width: 0.5rem
      background-color: rgba(255,255,255,0.4)
      border-radius: 8px
    &::-webkit-scrollbar-thumb
      background-color: rgba(255,255,255,0.8)
      border-radius: 8px
    @media (max-width: 600px)
      padding-right: 4px
  &__item
    margin-left: 8px
    width: 240px
    margin: 0 auto
    margin-bottom: 2rem
    a
      display: block
      position: relative
      text-decoration: none
      color: $modalTextColor
  &__img
    position: relative
    img
      width: 100%
    .tag
      &__list
        position: absolute
        left: 0
        right: 0
        top: 0
        bottom: 4px
        background-color: rgba(0,0,0,.5)
        opacity: 0
        transition: 0.2s
        font-size: 1.8rem
        font-weight: 400
        display: flex
        flex-wrap: wrap
        justify-content: space-evenly
        align-content: center
        padding: 12px
        padding-left: 8px
        &:hover
          opacity: 1
          transition: 0.2s
      &__item
        color: $textPinkColor
        background-color: rgba(0,0,0,.5)
        border: 1px solid
        border-radius: 8px
        padding: 6px
        margin-top: 12px
  &__title
    font-size: 2rem
    @media (max-width: 600px)
      font-size: 2.5rem
  &__desc
    font-size: 1.2rem
    @media (max-width: 600px)
      font-size: 1.8rem


@keyframes fadeIn 
  0% 
    background: rgba(0,0,0,.0)
  
  100% 
    background: rgba(0,0,0,.7)

@keyframes fadeOut 
  0% 
    background: rgba(0,0,0,.7)
  
  100% 
    background: rgba(0,0,0,.0)
    z-index: -1
    visibility: hidden

@keyframes modalFadeIn 
  0% 
    background-color: transparent
  
  100% 
    background-color: $modalBgColor

@keyframes modalFadeOut 
  0% 
    background-color: $modalBgColor
  
  100% 
    background-color: transparent

@keyframes modalContentFadeIn 
  0% 
    opacity: 0
    top: -20px
  
  100% 
    opacity: 1
    top: 0

@keyframes modalContentFadeOut 
  0% 
    opacity: 1
    top: 0px
  
  100% 
    opacity: 0
    top: -20px
  
@keyframes modalCloseBtnFadeIn 
  0% 
    opacity: 0
    top: -20px
  
  100% 
    opacity: 1
    top: 10px

@keyframes sketchIn
  0%
    stroke-dashoffset: 300
    opacity: 1
  90%
    stroke-dashoffset: 0
  100%
    stroke-dashoffset: 0
    opacity: 0
